import React from "react";
import { connect } from "dva";
import { Form, Input, Button, Icon } from "antd";
import styles from "./index.less";



const FormItem = Form.Item;
const formHead = (props: any) => {

  console.log("问问sd");

  const { form } = props;

  const { getFieldDecorator } = form;
  const handleSubmit = (e: any) => {
    e.preventDefault();
    props.form.validateFields((err: any, values: any) => {
      if (!err) {
        if (values.username == "lanlan" && values.password == "123456") {
          sessionStorage.setItem("token", "lanlan");
          props.history.push("/testOne");
        }
      }
    });
  };
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 10 }
    }
  };
  const tailFormItemLayout = {
    wrapperCol: {
      xs: {
        span: 24,
        offset: 0
      },
      sm: {
        span: 16,
        offset: 6
      }
    }
  };



  return (

    <div>

     
      
       
        <div className={styles.nameAll}>
         
          <Form>
            <Form
              {...formItemLayout}
              onSubmit={e => handleSubmit(e)}
              className="login-form"
            >
              <FormItem label={"请输入用户名"}>
                {getFieldDecorator("username", {
                  rules: [{ required: true, message: "请输入您的用户名!" }]
                })(
                  <Input
                    prefix={
                      <Icon type="user" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    placeholder="用户名"
                  />
                )}
              </FormItem>
              <FormItem label={"请输入您的密码"}>
                {getFieldDecorator("password", {
                  rules: [{ required: true, message: "请输入您的密码" }]
                })(
                  <Input
                    prefix={
                      <Icon type="lock" style={{ color: "rgba(0,0,0,.25)" }} />
                    }
                    type="password"
                    placeholder="密码"
                  />
                )}
              </FormItem>
    
              <FormItem {...tailFormItemLayout}>
                <Button
                  style={{ width: 160 }}
                  type="primary"
                  htmlType="submit"
                  className="login-form-button"
                >
                  登录
                </Button>
              </FormItem>
            </Form>
          </Form>
        </div>
      
    </div>
   
  );
};
const FormHead = Form.create({ name: "time_related_controls" })(formHead);
export default connect(
  (state: any) => {
    return {};
  },
  (dispatch: any) => {
    return {};
  }
)(FormHead);

